import { mapActions } from 'vuex'
export default {
  data() {
    return {}
  },
  methods: {
    ...mapActions('login', ['signout'])
  },
  computed: {
    drawer: {
      get() {
        return this.$store.state.admin.drawer
      },
      set(val) {
        this.$store.commit('admin/updateDrawer', val)
      }
    },
    title() {
      return this.$store.state.admin.title
    }
  }
}
